$primaryColor: #4527a4;

.wp-block-b-blocks-test-purpose {
	box-sizing: border-box;

	* {
		box-sizing: border-box;
	}

	.bBlocksTestPurpose {
		// display: inline-block;
		p{
			padding: 10px;
			border-radius: 10px;
		}
		
	}

	.icon {
		font-style: inherit;
	}

	img {
		width: 100%;
		max-width: 100%;
	}
}


  
.profile-card {
	width: 600px;
	background-color: #222;
	color: #fff;
	border-radius: 10px;
	overflow: hidden;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  .button-1,.button-2{
	display: flex;
	justify-items: center;
	align-items: center;
	gap: 8px;
	text-decoration: none;
  }
  .button-2{
	margin-left: 40px;
	background-color: #0077b5;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 20px;
	padding-right: 20px;
	border-radius: 10px;

  }
  .button-1{

	background-color: #fff;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 20px;
	padding-right: 20px;
	border-radius: 10px;
	color: black;

  }

  
  .profile-photo {
	width: 100%;
	height: 100%;
	object-fit: cover;
	border: 2px solid #fff;
  }
  
  .profile-info {
	padding: 20px;
	text-align: center;
  }
  
  h2 {
	margin: 10px 0;
	font-size: 24px;
  }
  
  .job-title {
	color: #aaa;
	font-size: 14px;
  }
  
  .website {
	color: #ddd;
	font-size: 14px;
	margin-bottom: 15px;
  }
  
  .buttons {
	display: flex;
	justify-content: center;
	gap: 10px;
	margin-bottom: 20px;
  }
  
 
  


  .about, .interests {
	margin-bottom: 20px;
  }
  
  .about h3, .interests h3 {
	margin-bottom: 10px;
	font-size: 16px;
  }
  
  .social-links {
	display: flex;
	justify-content: center;
	gap: 15px;
  }
  
  .social-links img {
	width: 60px;
	height: 60px;
	border-radius: 20px;
	
  }
  

.new-tab{
	margin-top: 10px;
}

#about-title,#about-des{
	text-align: start;
}
#about-des{
	font-size: 18px;
}
.gmail{
	width: 20px;
	height: 20px;
}
.linkedin{
	width: 20px;
	height: 20px;
	background-color: #0077b5;

}